<template>
  <div class="machine-details">
    <div class="crumbs">
      <span class="crumbs-item"></span>
      <router-link class="crumbs-item" :to="{name:'Cluster'}"></router-link>
      <router-link class="crumbs-item" :to="{name:'Cluster-Details', params:{id:1}}">Cluster 1</router-link>
      <span class="qingse-text">TIMCOOO1</span>
    </div>
    <div class="v-content">
      <div class="info-panel head margin-b-24">
        <div class="btns-group alter-button">
          <button class="comb-btn waves-effect lvse" @click="alter" v-bind:class="{ hide:toAlter }"></button>
          <button class="comb-btn waves-effect lvse" @click="submitAlter" v-bind:class="{ hide:altering }"></button>
          <button class="comb-btn waves-effect lvse" @click="cancelAlter" v-bind:class="{ hide:altering }"></button>
        </div>
        <div class="info-left">
          <div class="info-icon"><img class="vam" src="../assets/cluster-avatar.png" alt=""></div>
        </div>
        <div class="info-right article">
    			<h5 class="qingse-text editable">{{ this.name }}</h5>
          <p>
            <span></span>
            <span class="lanse-text"></span>
          </p>
          <p>
            <span></span>
            <span class="lvse-text"></span>
          </p>
          <div class="toggle-machine-status">
            <tips text=""><i class="ten-icon">&#xe734;</i></tips>
            <tips text=""><i class="ten-icon">&#xe606;</i></tips>
            <tips text=""><i class="ten-icon">&#xe669;</i></tips>
          </div>
          <div class="machine-info">
            <div>IP: 127.0.108.11</div>
            <div></div>
            <div></div>
          </div>
    		</div>
    	</div>
      <div class="margin-b-16"></div>
      <div class="panel-tab margin-b-24">
        <tabs :labels="['','','']" theme="block" :tabkey="1">
          <div>neirong1</div>
          <div>
            <table class="striped highlight">
              <col width="15%">
              <col width="15%">
              <col width="50%">
              <col width="20%">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="app in apps">
                  <td>{{ app.version }}</td>
                  <td>{{ app.create_time }}</td>
                  <td>{{ app.path }}</td>
                  <td>
                    <span class="lvse-text table-details_btn"></span>
                    <span class="qingse-text table-details_btn" @click="moveMachine"></span>
                    <span class="hongse-text table-details_btn" @click="delMachine"></span>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="clearfix"><pages class="right" :allpage="5" :nowpage="1" @change="pageChange"></pages></div>
          </div>
          <div>neirong3</div>
        </tabs>
      </div>
      <div class="margin-b-16"></div>
      <div class="panel-tab margin-b-24">
        <tabs :labels="['','','']" theme="block" :tabkey="1">
          <div>neirong1</div>
          <div>neirong2</div>
          <div>neirong3</div>
        </tabs>
      </div>
    </div>
    <!--  -->
    <modal title="" buttons="," buttonsClass="comb-btn lvse,comb-btn qingse" ref="movemachine" class="comb-dialog mini">
      <tselect :data="clusters_to_move"></tselect>
    </modal>
    <!--  -->
    <modal buttons="," buttonsClass="comb-btn lvse,comb-btn qingse" ref="delmachine" class="comb-dialog mini">
      <div class="comb-dialog_info center-align red-text large"><i class="ten-icon">&#xe691;</i> <span v-html="delbody"></span></div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'machine-details',
  data () {
    return {
      toAlter: false,
      altering: true,
      title: '',
      details: '',
      delbody: '',
      apps: [],
      name: '',
      clusters_to_move: [
        {label: '1', active: true},
        {label: '2'},
        {label: '3'},
        {label: '4'},
        {label: '5'}
      ]
    }
  },
  methods: {
    delMachine () {
      this.delbody = ''
      this.$refs.delmachine.show()
    },
    moveMachine () {
      this.$refs.movemachine.show()
    },
    alter () {
      this.toAlter = true
      this.altering = false
      var editable = document.getElementsByClassName('editable')
      for (var i = 0; i < editable.length; i++) {
        editable[i].contentEditable = true
        if (editable[i].tagName === 'H5') {
          this.title = editable[i].innerHTML
        }
        if (editable[i].tagName === 'P') {
          this.details = editable[i].innerHTML
        }
      }
    },
    submitAlter () {
      this.toAlter = false
      this.altering = true
      var editable = document.getElementsByClassName('editable')
      for (var i = 0; i < editable.length; i++) {
        editable[i].removeAttribute('contenteditable')
      }
    },
    cancelAlter () {
      this.toAlter = false
      this.altering = true
      var editable = document.getElementsByClassName('editable')
      for (var i = 0; i < editable.length; i++) {
        editable[i].removeAttribute('contenteditable')
        if (editable[i].tagName === 'H5') {
          editable[i].innerHTML = this.title
        }
        if (editable[i].tagName === 'P') {
          editable[i].innerHTML = this.details
        }
      }
    },
    pageChange () {
      return
    }
  },
  mounted () {
    axios.get('static/api/clusters.json').then(response => {
      for (var i = 0; i < response.data.clusters.length; i++) {
        for (var j = 0; j < response.data.clusters[i].hosts.length; j++) {
          if (response.data.clusters[i].hosts[j].id === parseInt(this.$route.params.id)) {
            this.apps = response.data.clusters[i].hosts[j].apps
            this.name = response.data.clusters[i].hosts[j].name
          }
        }
      }
    })
  }
}
</script>

<style scpoed lang="scss">
  @import "../scss/cluster-details";
  @import "../scss/machine-details";
</style>



